
import React, { Component } from 'react';
import { Accordion, Button, Form } from 'react-bootstrap';
import AndOrCondition from './AndOr'
class AssetsTypes extends Component {
    render() {

        const { onAssetsApply, onSelectAllAssetsApply, state, conditionApply, filterApply, filterKey, indexFilter, isLast, onSelectActive } = this.props
        return <Accordion.Item eventKey={'assetsType'} className="pb-2">
            <div className="d-flex">
                <Accordion.Header className="me-1">
                    <div className="d-flex flex-grow-1 align-items-center">
                        <i className="ri-image-line text-gray-800 icon-7 me-1"></i>
                        <div className="fs-9 fw-medium">Assets Type</div>
                    </div>
                    <Button variant='link' size={'sm'} className="close_filter position-absolute end-0 top-0 w-32px h-32px" onClick={filterApply(filterKey)}></Button>
                </Accordion.Header>
                <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
            </div>
            <Accordion.Body className="border rounded-1 mt-1">
                <div className="navi-item d-flex align-items-center py-2 cursor-pointer border-bottom">
                    <label className="form-check flex-grow-1 fs-9 fw-medium mb-0" htmlFor="allFilAssets">Select All</label>
                    <Form.Check type="checkbox" id="allFilAssets" checked={Object.keys(state.assetsApply).length >= 3 ? true : false} onClick={onSelectAllAssetsApply} />
                </div>


                <div className="dropdown-item px-0 border-bottom rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="image_assets">
                        <div className="symbol symbol-26 bg-light me-2">
                            <div className="symbol-label">
                                <i className="ri-image-line text-gray-800 icon-7 me-0"></i>
                            </div>
                        </div>
                        <p className="fs-9 fw-medium mb-0">Image Assets</p>
                    </label>
                    <Form.Check type="checkbox" id="image_assets" className="flex-shrink-0" checked={Object.keys(state.assetsApply).includes("2,4") ? true : false} onClick={onAssetsApply("2,4")} />
                </div>


                <div className="dropdown-item px-0 border-bottom rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="video_assets">
                        <div className="symbol symbol-26 bg-light me-2">
                            <div className="symbol-label">
                                <i className="ri-movie-line text-gray-800 icon-7 me-0"></i>
                            </div>
                        </div>
                        <p className="fs-9 fw-medium mb-0">Video Assets</p>
                    </label>
                    <Form.Check type="checkbox" id="video_assets" className="flex-shrink-0" checked={Object.keys(state.assetsApply).includes("3,5") ? true : false} onClick={onAssetsApply("3,5")} />
                </div>

                <div className="dropdown-item px-0 rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="text_assets">
                        <div className="symbol symbol-26 bg-light me-2">
                            <div className="symbol-label">
                                <i className="ri-text text-gray-800 icon-7 me-0"></i>
                            </div>
                        </div>
                        <p className="fs-9 fw-medium mb-0">Text Assets</p>
                    </label>
                    <Form.Check type="checkbox" id="text_assets" className="flex-shrink-0" checked={Object.keys(state.assetsApply).includes("1") ? true : false} onClick={onAssetsApply("1")} />
                </div>
            </Accordion.Body>
        </Accordion.Item>

    }
}

export default AssetsTypes