
import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import { Accordion, Button, Form } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../../Services/Utils/utils';
import AndOrCondition from './AndOr'
class Network extends Component {
    state = {
        networkList: null
    }
    componentDidMount() { this.feedNetworkList() }
    componentDidUpdate(prevProps) {
        if (prevProps.state.networkData != this.props.state.networkData) this.feedNetworkList()
    }

    feedNetworkList = () => {
        const { feedData, networkData } = this.props.state
        let networkConnectionId = []
        networkConnectionId = feedData.map((item) => item.networkId)
        networkConnectionId = [...new Set(networkConnectionId)]
        if (networkConnectionId.includes(18)) { networkConnectionId.splice(networkConnectionId.indexOf(18), 1, 2) }
        const networksList = networkData.filter(network => networkConnectionId.includes(network.id))
        this.setState({ networkList: networksList })
    }

    render() {
        const { state, onNetworkApply, onClickSelectAll, onSearchFilter, filterApply, filterKey, conditionApply, indexFilter, isLast, onSelectActive } = this.props
        const { networkList } = this.state

        return <Accordion.Item eventKey={'source'} className="pb-2">
            <div className="d-flex">
                <Accordion.Header className="me-1">
                    <div className="d-flex flex-grow-1 align-items-center">
                        <span className="svg-icon text-gray-800 svg-icon-7 d-flex">
                            <SVG src={baseUrl('media/icons/svg-icons/network.svg')} height={16} width={16} />
                        </span>
                        <div className="fs-9 fw-medium">Network</div>
                    </div>
                    <Button variant='link' size={'sm'} className="close_filter position-absolute end-0 top-0 w-32px h-32px" onClick={filterApply(filterKey)}></Button>
                </Accordion.Header>
                <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
            </div>
            <Accordion.Body className="border rounded-1 mt-1">

                <>
                    <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                        <label className="form-check flex-grow-1 fs-9 fw-medium mb-0" htmlFor="searchNetwork">
                            Search Network
                        </label>
                        <Form.Check type="checkbox" onClick={onClickSelectAll} checked={networkList && (Object.keys(state.selectedNetwork).length >= networkList.length) && networkList.length != 0 ? true : false} />
                    </div>
                    <div className="form-group mb-2">
                        <Form.Control type="text" size={'sm'} id="searchNetwork" placeholder="Search your network..." onChange={onSearchFilter} />
                    </div>
                    {networkList && networkList.length > 0 ?
                        <>
                            {
                                networkList.map((item) => {
                                    return <div className="dropdown-item px-0 border-bottom rounded-0" >
                                        <label className="form-check flex-grow-1 overflow-hidden align-items-center fs-9 fw-medium" htmlFor={`filter_${GET_NETWORK_ICON(item.id)}`}>
                                            <span className="symbol symbol-22">
                                                <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(item.id)}.svg`)} height={20} width={20} alt={item.name} />
                                            </span>
                                            <span className="form-check-label text-gray-800 text-ellipsis">{item.name}</span>
                                        </label>
                                        <Form.Check type="checkbox" className="flex-shrink-0" id={`filter_${GET_NETWORK_ICON(item.id)}`} checked={Object.keys(state.selectedNetwork).includes(`${item.id}`) ? true : false} onClick={onNetworkApply(item.id)} />
                                    </div>
                                })
                            }

                        </> :
                        <div className="py-4">
                            <h6 className="text-center fs-8 mb-0">No Networks Available</h6>
                        </div>
                    }
                </>

            </Accordion.Body>
        </Accordion.Item>

    }
}

export default Network;