import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Form, Button } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import AndOrCondition from './AndOr'
class UgcRights extends Component {
    render() {
        const { onSelectAllUgcRigths, onUgcRigths, state, conditionApply, filterApply, filterKey, indexFilter, isLast, onSelectActive } = this.props
        return <Accordion.Item eventKey={'ugcrights'} className="pb-2">
            <div className="d-flex">
                <Accordion.Header className="me-1">
                    <div className="d-flex flex-grow-1 align-items-center">
                        <span className="svg-icon text-gray-800 svg-icon-7 d-flex">
                            <SVG src={baseUrl('media/icons/svg-icons/rights-approved-line.svg')} height={18} width={18} />
                        </span>
                        <div className="fs-9 fw-medium">UGC Rights</div>
                    </div>
                    <Button variant='link' size={'sm'} className="close_filter position-absolute end-0 top-0 w-32px h-32px" onClick={filterApply(filterKey)}></Button>
                </Accordion.Header>
                <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
            </div>

            <Accordion.Body className="border rounded-1 mt-1">

                <div className="navi-item d-flex align-items-center py-2 cursor-pointer border-bottom">
                    <label className="form-check flex-grow-1 fs-9 fw-medium mb-0" htmlFor="allFilUgc">Select All</label>
                    <Form.Check type="checkbox" id="allFilUgc" checked={Object.keys(state.ugcRigths).length >= 3 ? true : false} onClick={onSelectAllUgcRigths} />
                </div>

                <div className="dropdown-item px-0 border-bottom rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="ugcFilApproved">
                        <div className="symbol symbol-26 bg-light me-2">
                            <div className="symbol-label">
                                <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-approved-line.svg')} height={18} width={18} />
                                </span>
                            </div>
                        </div>
                        Approved
                    </label>
                    <Form.Check type="checkbox" id="ugcFilApproved" className="flex-shrink-0" checked={Object.keys(state.ugcRigths).includes("2") ? true : false} onClick={onUgcRigths("2")} />
                </div>


                <div className="dropdown-item px-0 border-bottom rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="ugcFilPending">
                        <div className="symbol symbol-26 bg-light me-2">
                            <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                <SVG src={baseUrl('media/icons/svg-icons/rights-pending-line.svg')} height={18} width={18} />
                            </span>
                        </div>
                        Pending
                    </label>
                    <Form.Check type="checkbox" id="ugcFilPending" className="flex-shrink-0" checked={Object.keys(state.ugcRigths).includes("1") ? true : false} onClick={onUgcRigths("1")} />
                </div>
                <div className="dropdown-item px-0 rounded-0">
                    <label className="fs-9 fw-medium mb-0 d-flex align-items-center flex-grow-1" htmlFor="ugcFilRejected">
                        <div className="symbol symbol-26 bg-light me-2">
                            <div className="symbol-label">
                                <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                    <SVG src={baseUrl('media/icons/svg-icons/rights-rejected-line.svg')} height={18} width={18} />
                                </span>
                            </div>
                        </div>
                        Rejected
                    </label>
                    <Form.Check type="checkbox" id="ugcFilRejected" className="flex-shrink-0" checked={Object.keys(state.ugcRigths).includes("3") ? true : false} onClick={onUgcRigths("3")} />
                </div>
            </Accordion.Body>
        </Accordion.Item>

    }
}

export default UgcRights
