
import React, { Component } from 'react';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip,Button,Form } from 'react-bootstrap';
import AndOrCondition from './AndOr'
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import CollectionThumb from '../../../CommonComponents/CollectionThumb';
class Collections extends Component {
    render() {
        const { state, onCollectionSearchFilter, onCollectionSelectAll, onCollectionApply, conditionApply, filterApply, filterKey, indexFilter, isLast, onSelectActive } = this.props
        return  <Accordion.Item eventKey={'collection'} className="pb-2">
                <div className="d-flex">
                    <Accordion.Header className="me-1">
                        <div className="d-flex flex-grow-1 align-items-center pe-4">
                            <i className="ri-folder-4-line text-gray-800 icon-7 me-1"></i>
                            <div className="fs-9 fw-medium">Collection</div>
                        </div>
                        <Button variant='link' size={'sm'} className="close_filter position-absolute end-0 top-0 w-32px h-32px" onClick={filterApply(filterKey)}></Button>
                        
                    </Accordion.Header>
                    <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
                </div>
                <Accordion.Body className="border rounded-1 mt-1">
                    
                        <>
                        <div className="navi-item d-flex align-items-center py-2 cursor-pointer">
                            <label className="form-check flex-grow-1 fs-9 fw-medium mb-0" htmlFor="searchFilCollection">
                            Search Collection
                            </label>
                            <Form.Check type="checkbox" onClick={onCollectionSelectAll} checked={Object.keys(state.selectedCollection).length >= state.collectionCompleteList.length ? true : false}/>
                        </div>
                        <div className="form-group mb-2">
                            <Form.Control type="text" size={'sm'} id="searchCollection"  placeholder="Search your collection..." onChange={onCollectionSearchFilter}  />
                        </div>
                            {state.collectionList && Object.keys(state.collectionList).length > 0 ?    
                                <>
                                    {
                                        state.collectionList.map((item, indexC) => <div key={indexC} className="dropdown-item px-0"><label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`collection_${item.id}`}>
                                            <CollectionThumb symbolClasses={'symbol symbol-22 bg-light'} iconClass={'text-gray-700 me-0 fw-normal'} thumb={item.thumbnails} thumbName={item.name} />
                                            <span className="form-check-label text-gray-800 text-ellipsis">{item.name}</span>
                                        </label>
                                        <Form.Check type="checkbox"  name={`collection_${item.id}`} id={`collection_${item.id}`} checked={Object.keys(state.selectedCollection).includes(`${item.id}`) ? true : false} onClick={onCollectionApply(item.id)} />
                                            
                                        </div>)
                                    }
                                </>:
                                <div className="py-4">
                                    <h6 className="text-center fs-9 mb-0 text-gray-700">{state.isCollection ? `Loading...` : `No Collections Available`}</h6>
                                </div>
                            }
                        </>
                </Accordion.Body>
            </Accordion.Item>
        
    }
}

export default Collections
