
import React, { Component } from 'react';
import ReactHtmlParser from 'react-html-parser';
import SVG from 'react-inlinesvg';
import { Accordion, Button, Form } from 'react-bootstrap';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../../Services/Utils/utils';
import AndOrCondition from './AndOr'

class Connection extends Component {


    render() {
        const { state, onconnectionApply, onConnectionSelectAll, conditionApply, filterApply, filterKey } = this.props
        return <Accordion.Item eventKey={'connection'} className="pb-2">
            <div className="d-flex">
                <Accordion.Header className="me-1">
                    <div className="d-flex flex-grow-1 align-items-center">
                        <span className="svg-icon text-gray-800 svg-icon-7 d-flex">
                            <SVG src={baseUrl('media/icons/svg-icons/network-manager.svg')} height={17} width={17} />
                        </span>
                        <div className="fs-9 fw-medium">Connections</div>
                    </div>
                    <Button variant='link' size={'sm'} className="close_filter position-absolute end-0 top-0 w-32px h-32px" onClick={filterApply(filterKey)}></Button>
                </Accordion.Header>
                <AndOrCondition condition={state.condition} conditionApply={conditionApply} />
            </div>
            <Accordion.Body className="border rounded-1 mt-1">
                {state.feedData && Object.keys(state.feedData).length > 0 ? <>
                    <div className="navi-item d-flex align-items-center py-2 cursor-pointer border-bottom">
                        <label className="form-check flex-grow-1 fs-9 fw-medium mb-0" htmlFor="allConnection">Select All</label>
                        <Form.Check type="checkbox" id="allConnection" onClick={onConnectionSelectAll} checked={Object.keys(state.selectedConnection).length >= state.feedData.length ? true : false} />
                    </div>
                    <>
                        {state.feedData.map((item) => <div className="dropdown-item px-0 border-bottom rounded-0" >
                            <label className="form-check flex-grow-1 overflow-hidden align-items-center fs-9 fw-medium" htmlFor={item.feedId}>
                                <span className="symbol symbol-22">
                                    <img className="img-fluid" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={20} width={20} alt={''} />
                                </span>
                                <span className="form-check-label text-gray-800 text-ellipsis">{ReactHtmlParser(item.name)}</span>
                            </label>
                            <Form.Check type="checkbox" className="flex-shrink-0" id={item.feedId} name={item.feedId} value={item.feedId} onClick={onconnectionApply(item.feedId)} checked={Object.keys(state.selectedConnection).includes(`${item.feedId}`) ? true : false} />
                        </div>)
                        }
                    </>
                </>
                    :
                    <div className="py-4">
                        <h6 className="text-center fs-8 mb-0">No Connections Available</h6>
                    </div>
                }
            </Accordion.Body>
        </Accordion.Item>
    }
}
export default Connection;